/*文章样式部分*/
.content aside {
    min-height: 0;
}

.article {
    width: 100%;
    min-height: 600px;
    background-color: #fff;
    border-radius: 4px;

    clear: both;
    display: block;
    width: 100%;
    margin: 10px auto;
    color: gray;
    padding: 25px;
    line-height: 1.7em;
}

.article > span {
    display: block;
    padding: 10px 30px;
    color: gray;
    font-size: 0.9em;
}

.article .mold {
    float: left;
}

.article .mold a {
    text-decoration: none;
    color: gray;
}

.article .mold a:hover {
    color: lightblue;
}

.article .reading span {
    color: lightblue;
}

/* .article>h2 {
    text-align: center;
    padding: 10px 0;
    color: gray;
} */



.article h2,.article h3,.article h4,.article h5,.article h6 {
    text-indent: 1em;
    padding: 8px 0;
    color: #00cddf;
    text-shadow: 2px 0 5px #eae4e4;
}

@media screen and (max-width: 846px) {
    .article h2, h3, h4, h5, h6 {
        text-indent: 0em;
        text-align:center
    }
}

.article ul {
    list-style: none;
    display: block;
    width: 84%;
    margin: 0 auto;
}

.article > ul > li {
    display: block;
    float: left;
    font-size: 0.9em;
    width: 23%;
    text-align: center;
    color: gray;
}

.article > ul > li:first-child {
    width: 31%;
}

.article hr {
    clear: both
}

.article article p {
    display: block;
    clear: both;
    font-size: 0.95em;
}


@media screen and (max-width: 846px) {
    .article article ul {
        width: 100%;
    }

    .article article > ul > li {
        width: 100%;
    }
}



